<template>
  <div class="topbar-wrapper">
    <span v-if="showBack" class="back" @click="backToPre"><</span>
    <h3 class="title">{{title}}</h3>
  </div>
</template>

<script>
  //  import router from 'vue-router'
  export default{
    props: {
      title: {
        type: String
      },
      showBack: {
        type: Boolean
      }
    },
    mounted: function () {
      console.log(this.title)
    },
    methods: {
      backToPre: function () {
        this.$router.back(-1)
      }
    }
  };
</script>

<style lang="stylus">
  .topbar-wrapper
    width: 100%
    height: 30px
    line-height: 30px
    background: #d81e06
    color: #fff
    font-size: 14px
    position: fixed
    top: 0
    left: 0
    z-index: 999
    .back
      position: absolute
      top: 0
      left: 0
      padding: 0 15px
      font-size: 16px
    .title
      text-align: center
      width: 100%

</style>



