<template>
  <div class="container">
    <form action="#" method="get" @submit.prevent="doSubmit">
      <input type="text" placeholder="用户名" id="username" :value="uname">
      <input type="password" placeholder="密码" id="password" :value="pws">
      <input type="submit" value="登录">
    </form>
  </div>
</template>

<script>
  export default{
    data: function () {
      return {
          uname: 'lh',
          pws: '123'
      }
    },
    methods: {
      doSubmit: function () {
        var username = this.$el.querySelector('#username').value
        var password = this.$el.querySelector('#password').value
        var url = 'http://localhost:3000/login?username=' + username + '&password=' + password
        this.$http.jsonp(url).then( (res) =>{
          console.log(res.body.msg)
        }, () =>{

        })
      }
    }
  };
</script>

<style lang="stylus">
  html, body
    height: 100%
    .container
      height: 100%
      background: skyblue
      display: flex
      justify-content: center
      align-items: center
      form
        width: 80%
        text-align: center
        input
          width: 80%
          margin-top: 10px
          border: 1px solid #fff
          height: 40px
</style>



