<template>
  <div class="movieMore-wrapper">
    <topbar :title="title" :showBack="true"></topbar>
    <loading v-if="isDone"></loading>
    <div class="movies-wrapper">
      <movieitem v-for="movie in movies" :movie="movie"></movieitem>
    </div>
  </div>
</template>

<script>
  var myutil = require('../../common/js/myutil')
  import movieitem from "@/components/movie/movieItem/movieItem.vue"
  import topbar from "@/components/topbar/topbar.vue"
  import loading from "@/components/loading/loading.vue"
  export default{
    data() {
      return {
        title: '',
        movies: []
      }
    },
    props: {
//      movies: {
//        type: Object
//      }
    },
    computed: {
      isDone: function () {
        if (this.movies.length > 0) return false;
        else return true;
      }
    },
    components: {
      movieitem,
      topbar,
      loading
    },
    mounted: function () {
      console.log("in movieMore ,url:"+this.$route.params.url)
      var url = this.$route.params.url
      this.$http.jsonp(url).then(res => {
          this.title = res.data.title
        this.movies = myutil.convertMoviesJson2Arr(res.data.subjects)
      })
    }
  };
</script>

<style lang="stylus">
  .movieMore-wrapper
    padding-top: 40px
    width: 320px
    margin: 0 auto
    .movies-wrapper
      display: flex
      flex-direction: row
      flex-wrap: wrap

</style>



