var myutil = {
  //将星星评分转换成数组，如[1,1,1,0,0]
  converStar2Arr: function (score) {
    var arr = []
    for (var i = 0; i < 5; i++) {
      if (i < (score / 2 - 1)) {
        arr.push(1)
      } else {
        arr.push(0)
      }
    }
    return arr;
  },
  //将电影json数据解析成对象数组
  convertMoviesJson2Arr(subjects) {
    // console.log(subjects)
    var movies = []
    for (var i = 0; i < subjects.length; i++) {
      var item = subjects[i]
      var temp = this.convertMovieJson(item)
      movies.push(temp)
    }
    return movies;
  },
  //解析一个movie json
  convertMovieJson(obj) {
    var title = obj.title
    if (title.length > 5) {
      title = title.substring(0, 5) + ".."
    }
    var score = obj.rating.average
    var starArr = this.converStar2Arr(score)
    var coverImg = obj.images.large
    var id = obj.id

    var temp = {
      id: id,
      title: title,
      coverImg: coverImg,
      score: score,
      starArr: starArr
    }
    return temp
  },
  converMovieDetailJson(data) {
    console.log(data)
    var title = data.title
    var summary = data.summary
    var rating = data.rating.average
    var starScore = this.converStar2Arr(rating)
    var country = data.countries[0]
    var year = data.year
    var director = data.directors[0].name
    var image = data.images.large
    var genres = data.genres
    //看过人数
    var collectCount = data.collect_count
    var reviewsCount = data.reviews_count
    var casts = []
    for(var i = 0;i<data.casts.length;i++){
      var cast = data.casts[i]
      var name = cast.name
      if (name.length>6){
        var index = name.lastIndexOf("·") + 1
        name = name.substring(index,name.length)
      } else if (name.lastIndexOf("-") != -1 ){
        var index = name.lastIndexOf("-") + 1
        name = name.substring(index,name.length)
      }
      var id = cast.id
      var avatar = cast.avatars.large
      var temp = {
        id: id,
        avatar: avatar,
        name: name
      }
      casts.push(temp)
    }

    var obj = {
      title: title,
      summary:summary,
      rating: rating,
      starScore: starScore,
      country: country,
      year: year,
      director: director,
      casts: casts,
      image: image,
      genres: genres,
      collectCount: collectCount,
      reviewsCount: reviewsCount
    }
    console.log(obj)
    return obj
  }
}

module.exports = myutil



