<template>
  <div class="movie-list-wrapper">
    <div class="title-top clearfix">
      <h3 class="list-title">{{movieList.title}}</h3>
      <!--<router-link :to="{name:'moviemore',params: {url:moreUrl}}" class="more">更多</router-link>-->
      <p class="more" @click="toMorePage">更多</p>
    </div>
    <div class="movies-wrapper">
      <movieitem v-for="item in movieList.movies" :movie="item"></movieitem>
    </div>
  </div>
</template>

<script>
  var myutil = require('../../common/js/myutil')
  var myconst = require('../../common/js/const')
  import movieitem from "@/components/movie/movieItem/movieItem.vue"
  export default{
    data: function () {
      return {
      }
    },
    computed: {
//      moreUrl: function () {
//        if (this.title) {
//          console.log(this.title)
//          if (this.title.indexOf('正在')) {
//            return myconst.onUrl;
//          } else if (this.title.indexOf('Top')) {
//            return myconst.topUrl;
//          } else {
//            return myconst.comSoonUrl;
//          }
//        }
//      }
    },
    props: {
      movieList: {
        type: Object
      }
    },
    components: {
      movieitem
    },
    created: function () {
        console.log("movieList created")
    },
    methods: {
      toMorePage: function () {
        var title = this.$el.querySelector(".list-title").innerHTML;
        var url = '';
        if (title.indexOf('正在') !== -1) {
          url = myconst.onUrl;
        } else if (title.indexOf('Top') !== -1) {
          url = myconst.topUrl;
        } else {
          url = myconst.comSoonUrl;
        }
        this.$router.push({name:'moviemore',params: {url:url}})
      }
    }
  };
</script>

<style lang="stylus">
  .movie-list-wrapper
    margin-bottom: 10px
    background: #fff
    .title-top
      padding: 10px
      font-size: 14px
      .list-title
        float: left
      .more
        float: right
        margin-right: 6px
    .movies-wrapper
      display: flex
      flex-direction: row
      width: 320px
      margin: 6px auto

</style>



