<template>
  <div class="moviesIndex-container">
      <loading v-if="isDone"></loading>
      <movielist :movieList="inTheaters"></movielist>
      <movielist :movieList="top250"></movielist>
      <movielist :movieList="comSoon"></movielist>
  </div>
</template>

<script>
  var myutil = require('../../common/js/myutil')
  import movielist from "@/components/movie/movieList/movieList.vue"
  import loading from "@/components/loading/loading.vue"
  export default{
    data: function () {
      return {
        onUrl: 'https://api.douban.com/v2/movie/in_theaters',
        topUrl: 'https://api.douban.com/v2/movie/top250',
        comSoonUrl: 'https://api.douban.com/v2/movie/coming_soon',
        top250: {},
        inTheaters: {},
        comSoon: {}
      }
    },
    computed: {
      isDone: function () {
        if (this.top250.title && this.inTheaters.title && this.comSoon.title){
            return false;
        } else {
            return true;
        }
      }
    },
    created: function () {
      this.$http.jsonp("https://api.douban.com/v2/movie/top250?count=3").then(res => {
        this.top250 = this.processData(res.data)
      })
      this.$http.jsonp(this.onUrl + "?count=3").then(res => {
        this.inTheaters = this.processData(res.data)
      })
      this.$http.jsonp(this.comSoonUrl + "?count=3").then(res => {
        this.comSoon = this.processData(res.data)
      })
    },
    components: {
      movielist,
      loading
    },
    methods: {
      processData: function (data) {
        var movies = myutil.convertMoviesJson2Arr(data.subjects)
        var movieList = {
          title: data.title,
          movies: movies
        }
        return movieList
      }
    }
  };
</script>

<style lang="stylus">
  .moviesIndex-container
    padding-bottom: 38px
    overflow: hidden
</style>



