<template>
  <div class="movie-wrapper" v-bind="{'dataId': movie.id}" @click="toDetailPage">
    <img :src="movie.coverImg" class="movie-poster">
    <h3 class="movie-name">{{movie.title}}</h3>
    <star :arrs="movie.starArr" :score="movie.score"></star>
  </div>
</template>

<script>
  import star from "@/components/movie/star/star.vue"
  export default{
    props: {
      movie: {
        type: Object
      }
    },
    components: {
      star
    },
    methods: {
      toDetailPage: function (ev) {
        var id = ev.currentTarget.getAttribute("dataId");
        this.$router.push({name:'movieDetail',params:{ id:id }})
      }
    }
  };
</script>

<style lang="stylus">
  .movie-wrapper
    display: flex;
    flex-direction: column;
    /*flex: 1;*/
    align-items: center;
    margin-right: 10px
    width: 100px
    &:nth-child(3n)
      margin-right: 0
    .movie-poster
      width: 100px;
      height: 150px;
    .movie-name
      margin: 5px 0;
      color: #444;
      font-size: 14px;
</style>



