<template>
  <div class="movieDetail">
    <div class="topbar">
      <topbar :title="title" :showBack="true"></topbar>
    </div>
    <loading v-if="isDone"></loading>

    <div class="top">
      <div class="info">
        <h3 class="title">{{movie.title}}</h3>
        <div class="cy"><span>{{movie.country}}</span> · <span>{{movie.year}}</span></div>
        <div class="count">
          <p class="watched"><span class="countNum">{{movie.collectCount}}</span>人看过</p>
          <p class="comment"><span class="countNum">{{movie.reviewsCount}}</span>跳评论</p>
        </div>
      </div>
      <img :src="movie.image" alt="" class="poster">
      <img :src="movie.image" alt="" class="bg">
    </div>

    <div class="movieInfo border-bom">
      <div class="infoItem">
        <span class="title">评分: </span>
        <star :arrs="movie.starScore" :score="movie.rating"></star>
      </div>
      <div class="infoItem">
        <span class="title">导演: </span>
        <p class="content">{{movie.director}}</p>
      </div>
      <div class="infoItem">
        <span class="title">演员: </span>
        <p class="content">
          <span v-for="cast in movie.casts">{{cast.name}} </span>
        </p>
      </div>
      <div class="infoItem">
        <span class="title">类型: </span>
        <p class="content">
          <span v-for="genre in movie.genres">{{genre}} </span>
        </p>
      </div>
    </div>

    <div class="summary border-bom">
      <h3 class="cTitle">剧情简介</h3>
      <p class="content">{{movie.summary}}</p>
    </div>

    <div class="casts">
      <h3 class="cTitle">演员</h3>
      <div class="casts-wrap">
        <div class="cast" v-for="cast in movie.casts">
          <img class="avatar" :src="cast.avatar" alt="">
          <p class="name">{{cast.name}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  var myutil = require('../../common/js/myutil')
  import loading from "@/components/loading/loading.vue"
  import topbar from "@/components/topbar/topbar.vue"
  import star from "@/components/movie/star/star.vue"
  export default{
    data: function () {
      return {
        title: '',
        movie:{}
      }
    },
    computed: {
        isDone: function () {
          if (this.movie.title){
              return false;
          } else {
              return true;
          }
        }
    },
    created: function () {
      var id = this.$route.params.id;
      var url = "https://api.douban.com/v2/movie/subject/" + id;
      this.$http.jsonp(url).then(res => {
        console.log(res);
        var rs = res.body;
        this.title = rs.title;
        this.movie = myutil.converMovieDetailJson(rs);
      })
    },
    components: {
      loading,
      topbar,
      star
    }

  };
</script>

<style lang="stylus">
  .movieDetail
    .topbar
      padding-top: 30px
    .top
      color: #fff
      position: relative
      padding: 40px 0 0 20px
      height: 180px
      .info
        .title
          font-size: 28px
          font-weight: bold
          color: #444
        .cy
          margin: 30px 0
          color: #fff
          span
            font-size: 16px
        .count
          p
            display: inline-block
            .countNum
              color: red
              margin-right: 10px
          .comment
            margin-left: 10px
      .poster
        width: 100px
        height: 120px
        position: absolute
        bottom: -30px
        right: 10px
      .bg
        position: absolute
        z-index: -1
        width: 100%
        height: 100%
        top:0
        left: 0
        filter: blur(5px)
    .movieInfo
      padding: 20px 0 20px 10px
      .infoItem
        display: flex
        flex-direction: row
        margin-bottom: 6px
        .title
          font-size: 16px
          color: #aaa
          margin-right: 10px
        .content
          font-size: 14px
          color: #999
    .summary
      padding: 16px 10px
      color: #aaa
      .content
        margin-top: 10px
        text-indent: 2em
        font-size: 14px
        line-height: 18px
    .casts
      padding: 16px 10px
      color: #aaa
      .casts-wrap
        display: flex
        flex-direction: row
        flex-wrap: nowrap
        overflow-x: scroll
        margin-top: 10px
        .cast
          margin-right: 6px
          img
            width: 80px
            height: 100px
          .name
            text-align: center
    .border-bom
      border-bottom: 1px solid #ccc

</style>



