<template>
  <div id="app">
      <router-view></router-view>
    <div class="nav-bar">
      <div class="nav-item">
        <router-link class="nav-text" to="/index">
          <span class="nav-item-icon nav-item-icon1"></span><br>主页
          <!--<p>主页</p>-->
        </router-link>
      </div>
      <div class="nav-item">
        <router-link class="nav-text" to="/movie">
          <span class="nav-item-icon nav-item-icon2"></span>
          <p>电影</p>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'app',
    methods: {
      setNav: function () {
        this.isOnShow = !this.isOnShow;
      }
    }
  }
</script>

<style>
  a {
    text-decoration: none;
  }

  .nav-bar {
    background: #ccc;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    display: flex;
    flex-direction: row;
  }

  .nav-item {
    flex: 1;
    text-align: center;

  }

  .nav-item .nav-item-icon {
    width: 24px;
    height: 24px;
    display: inline-block;
    -webkit-background-size: 100%;
    background-size: 100%;
  }

  .nav-item-icon1 {
    background: url("../static/img/home.png") no-repeat center;

  }

  .nav-item-icon2 {
    background: url("../static/img/movie.png") no-repeat center;
  }

  .nav-text {
    text-decoration: none;
    display: block;
    font-size: 12px;
    color: #fff;
  }

  .router-link-active {
    color: #d81e06;
  }

  .router-link-active .nav-item-icon1 {
    background: url("../static/img/home-orange.png") no-repeat center;
    -webkit-background-size: 100%;
    background-size: 100%;
  }

  .router-link-active .nav-item-icon2 {
    background: url("../static/img/movie-orange.png") no-repeat center;
    -webkit-background-size: 100%;
    background-size: 100%;
  }

  .clearfix:before, .clearfix:after {
    content: '';
    display: table;
  }

  .clearfix:after {
    clear: both;
  }
</style>



