<template>
  <div class="star-container">
    <ul class="stars">
      <li v-for="num in arrs">
        <img src="../../../../static/img/star.png" v-if="num">
        <img src="../../../../static/img/none-star.png" v-if="!num">
      </li>
      <li class="score">{{score}}</li>
    </ul>
  </div>
</template>

<script>
    export default{
        props: {
          arrs: {
              type: Array
          },
          score: {
              type: Number
          }
        }
    };
</script>

<style lang="stylus">
  .stars
    li
      float: left
      margin-right: 2px
      img
        width: 10px
        height: 10px

</style>



